import Titling from "../home/titling"
import info from './info.js'
import ServiceCards from "./serviceCard"

const Services = () => (
    <main>
        
        <Titling
            title={info.title}
            subtitle={info.subtitle}
            className=""
        />

        <ServiceCards services={info.services} className="max-w-6xl mt-6 mx-auto" />
        
    </main>
)

export default Services

