import { Link } from "preact-router/match"
import Card from "../../components/general/card"
import Divider from "../../components/general/divider"

const ServiceCards = ({ className, services }) => (
    <div className={`${className} flex md:flex-row flex-col justify-around items-center flex-wrap`}>
        {services.map((service, index) => (
            <ServiceCard
                title={service.title}
                image={service.image}
                description={service.description}
            />
        ))}
    </div>
)

const ServiceCard = ({ title, image, description }) => (
    <Card className="max-w-xs group m-2" innerClassName="text-center py-4">
        
        <div className="overflow-hidden rounded-md inline-block aspect-video mx-4">
            <img src={image} className="group-hover:scale-110 transition-all object-cover aspect-video"></img>
        </div>
        
        <div className="font-bold text-xl mt-2 mx-4">
            {title}
        </div>

        {description && description.trim().length > 0 &&

            <>
                <Divider className="mt-4" />

                <div className="mt-4 text-gray-400 mx-4">
                    {description}
                </div>
            </>
        }

    </Card>
)

export default ServiceCards

